<template>
    <footer>
        <div id="new-contact" v-on:keyup.enter="onClick">
            <input id="contact-name"  type="text" placeholder="Enter New Contact Name" v-model="newContactName"/>
            <input id="contact-number" type="tel" placeholder="Enter Phone Number" v-model="newContactPhone" onkeypress="return event.charCode >= 48 && event.charCode <= 57"/>
            <span id="add-btn" v-on:click="onClick">Add New Contact</span>
        </div>
    </footer>
</template>

<script>
    export default {
        name: "NewContact",
        data: function () {
            return{
                newContactName: "",
                newContactPhone: ""
            }
        },
        methods:{
            onClick: function () {
                this.$emit("contact-add", this.newContactName, this.newContactPhone);
                this.newContactName = "";
                this.newContactPhone = "";
            }
        }
    }
</script>

<style scoped>
    footer {
        width: 100%;
        position: fixed;
        bottom: 0;
        background-image: linear-gradient( #41b883,#537387);
        box-shadow: 0 -5px 5px #41b883; 
    }

    #new-contact{
        margin: 0 auto;
        padding: 5px 0 10px;
        box-sizing: border-box;
        height: 160px;
        width: 400px;
        display: flex;
        flex-flow: column;
    }
    
    input {
        width: 80%;
        border: 2px solid white;
        border-radius: 20px;
        margin: 0px auto 5px;
        padding: 8px;
        background: transparent;       
        color: white;
        text-align: center;
        box-shadow: 5px 5px 5px #105b84;
    }

    input:focus {
        outline: none;
        border-width: 3px;
    }

    input::placeholder {
        color: white;
    }

    #add-btn {
        margin: 20px auto;
        padding: 5px 15px;
        border: 2px solid white;
        border-radius: 20px;
        cursor: pointer;
        user-select: none;
        box-shadow: 5px 5px 5px #105b84;
    }

    #add-btn:hover {
        border-width: 3px;
    }

    #add-btn:active {
        font-size: 0.97em;
    }

</style>