<template>
    <div id="app">
        <header><img src="./assets/logo.png" alt="Vue logo"><h2>Phonebook App</h2></header>
        <phonebook-directory v-bind:directory="directory"></phonebook-directory>
        <new-contact v-on:contact-add="contactAdd"></new-contact>
    </div>
</template>

<script>

    import NewContact from "./components/NewContact";
    import PhonebookDirectory from "./components/PhonebookDirectory";
    import Phonebook from "./model/Phonebook";
    import PhonebookItem from "./model/PhonebookItem";

    export default {
        name: 'App',
        components: {
            "new-contact": NewContact,
            "phonebook-directory": PhonebookDirectory
        },
        data: function () {
            return{
                directory: new Phonebook(),
            }
        },
        methods:{
            contactAdd: function (newContactName, newContactPhone) {
                let contact = new PhonebookItem(newContactName, newContactPhone)
                this.directory.add(contact);
            }
        }
    };
</script>


