<template>
    <div class="contact-box" v-bind:class="contact">
        <span class="contact-btns user-icon"></span>
        <span class="contact-box-content">{{ contact.name }} : </span>
        <span class="contact-box-content">{{ contact.phone }}</span>
        <a :href="'tel:' + contact.phone"><span class="contact-btns call-btn"></span></a>
        <span class="contact-btns delete-btn" title="Delete Contact" v-on:click.stop="onDelete"></span>
    </div>
</template>

<script>
    export default {
        name: "Contact",
        props: ["contact"],
        methods: {
            onDelete: function () {
                this.$emit("contact-delete");
            }
        }
    };
</script>

