import PhonebookItem from "./PhonebookItem";
class Phonebook {

    constructor() {
        this.contacts = this.loadFromLocalStorage();
    }

    add(contact) {
        if(!contact.name.trim().length){
            return
        }
        if(!contact.phone.match(/^[0-9]+$/) || contact.phone.length > 12){
            return
        }
        this.contacts.sort((a, b) => (a.name.toLowerCase() > b.name.toLowerCase()) ? 1 : -1);
        this.contacts.unshift(contact);
        this.saveToLocalStorage();
    }

    delete(contact) {
        this.contacts.splice(this.contacts.indexOf(contact), 1);
        this.saveToLocalStorage();
    }

    saveToLocalStorage() {
        localStorage.setItem('contact-data', JSON.stringify(this.contacts));
    }

    loadFromLocalStorage() {
        var json = localStorage.getItem('contact-data');

        if (json === null)
            return [];

        let jsonParsed = JSON.parse(json, (key, value) => {
            return value;
        });

        if (jsonParsed.length === 0)
            return [];

        let phonebookItems = [];

        for (let i = 0; i < jsonParsed.length; i++) {
            phonebookItems.push(PhonebookItem.fromJSON(jsonParsed[i]));
        }

        return phonebookItems;
    }

}
export default Phonebook;